
import Hub from "@/hub";
import { State, Action } from "vuex-class";
import { Options, Vue } from "vue-class-component";

// import HelloWorld from '@/components/HelloWorld.vue'; // @ is an alias to /src

@Options({
  components: {
    // HelloWorld,
  },
})
export default class Login extends Vue {
  username: string = localStorage.username;
  password!: string;
  @State("hub") hub!: Hub;
  @Action("tokenLogin") tokenLogin!: (token: string) => void;
  @Action("tips") tips!: (msg: string) => void;
  async clickLogin() {
    localStorage.username = this.username;
    if (!this.username || !this.password) {
      this.tips("用户名或密码不能为空");
      return;
    }
    var response = await this.hub.invoke(
      "Login",
      "Login",
      this.username,
      this.password
    );
    console.log(response);
    const msg = response[1];
    const state = response[0];
    if (state) {
      this.tips("登录成功");
      const token = msg;
      this.tokenLogin(msg);
    } else {
      this.tips("登录失败," + msg);
    }
  }
}
